.resumePage {
    width: 100%;
    padding-top: 7500px;
    padding-bottom: 2rem;
    background: transparent;
    min-height: 100vh;
}

.container {
    max-width: 100%;
    margin: 0 auto;
    padding: 0;
}

.resumeHeader {
    text-align: center;
    margin-bottom: 2rem;
    padding: 0 2rem;
}

.pageTitle {
    font-size: 3.5rem;
    color: #ffffff;
    margin-bottom: 2rem;
    font-weight: 700;
    background: linear-gradient(135deg, #ff3f81, #ff69a3);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.controls {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    margin-bottom: 2rem;
}

.languageSelector {
    display: flex;
    gap: 1rem;
}

.languageBtn {
    padding: 0.8rem 1.5rem;
    background: transparent;
    border: 2px solid rgba(255, 63, 129, 0.3);
    border-radius: 25px;
    color: #ff3f81;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 1rem;
}

.languageBtn:hover {
    border-color: #ff3f81;
    background: rgba(255, 63, 129, 0.1);
}

.languageBtn.active {
    background: linear-gradient(135deg, #ff3f81, #ff69a3);
    border-color: #ff3f81;
    color: white;
}

.downloadButton {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.8rem 1.5rem;
    background: linear-gradient(135deg, #ff3f81, #ff69a3);
    color: white;
    border: none;
    border-radius: 25px;
    font-weight: 500;
    font-size: 1rem;
    cursor: pointer;
    transition: all 0.3s ease;
}

.downloadButton:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 25px rgba(255, 63, 129, 0.4);
}

.pdfViewer {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 0 2rem;
}

.pdfDocument {
    width: 100%;
    max-width: 900px;
}

.pdfPage {
    margin: 0 auto 2rem auto;
    display: block !important;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.pdfPage canvas {
    width: 100% !important;
    height: auto !important;
}

@media (max-width: 768px) {
    .resumePage {
        padding-top: 140px;
    }
    
    .controls {
        flex-direction: column;
        gap: 1rem;
    }
    
    .pdfViewer {
        padding: 0 1rem;
    }
}

@media (max-width: 480px) {
    .resumeHeader {
        padding: 0 1rem;
    }
    
    .pageTitle {
        font-size: 2rem;
    }
    
    .pdfViewer {
        padding: 0 0.5rem;
    }
}